const ContactUs = (props) => {
    return (
        <Styled className="ContactUs-container">
            <Title>
                ContactUs
            </Title>
            <div className="wrapper">
                {/* <form action="action_page.php"> */}
                <form action="action_page.php">

                    <label for="fname">
                        <p>First Name</p>
                        <input type="text" id="fname" name="firstname" placeholder="Your name.." />
                    </label>

                    <label for="lname">
                        <p>Last Name</p>
                        <input type="text" id="lname" name="lastname" placeholder="Your last name.." />
                    </label>

                    <label for="country">
                        <p>Country</p>
                        <select id="country" name="country">
                            {countryArray.map((element, index) => {
                                return (
                                    element.name == "Ukraine"
                                        ? <option selected={true} value={element.name} key={index}>{element.name}</option>
                                        : <option value={element.name} key={index}>{element.name}</option>
                                )
                            })}
                        </select>
                    </label>

                    <label for="subject">
                        <p>Subject</p>
                        <textarea id="subject" name="subject" placeholder="Write something.." rows="10" cols="45"></textarea>
                    </label>

                    <input type="submit" value="Submit" />
                </form>
            </div>

        </Styled>
    )
};

export default ContactUs;

const Styled = styled.section`

form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

input, textarea, select {
    font-family: sans-serif;
    width: 100%;
    resize: none;
    padding: 1rem 0.5rem;
    font-size: 2rem;
    border: 0.1rem solid var(--color2);
    color: var(--color1);
    background-color: var(--bg);
    text-transform: none;    
}

input::placeholder, textarea::placeholder {
    font-family: "BebasNeue";
    font-size: 1.5rem;
    color: var(--color2);
}

input[type=submit] {
    display: inline-block;
    padding: 1.25rem 1rem;
    font-size: 2rem;
    line-height: 2rem;
    border: none;
    background-color: var(--color5);
    border-radius: 0.5rem;
    color: var(--color4);
    cursor: pointer;
    transition: box-shadow 0.2s;
    width: max-content;
}
`;